.table{width:100%;border-collapse:collapse}.table>:first-child{border-bottom:1px solid #e6eff5}.table>thead>tr{border-bottom:1px solid #e6eff5;height:35px}.table>thead>tr>td{vertical-align:top;color:var(--text-color);white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.table>tbody>tr{border-bottom:1px solid #e6eff5;height:60px}.table>tbody>tr>td{color:var(--black);white-space:nowrap;overflow:hidden;text-overflow:ellipsis;letter-spacing:.8}.table>tbody>tr>td>button{color:var(--text-color);background:none;border:1px solid var(--text-color);height:35px;white-space:nowrap;border-radius:25px;font-size:clamp(0.75rem,0.6839788732rem + 0.2816901408vw,0.9375rem);font-weight:400;padding:0 clamp(0rem,-0.5941901408rem + 2.5352112676vw,1.6875rem);transition:background,color,border,var(--transition)}@media(any-hover: hover){.table>tbody>tr>td>button:hover{border:none;background-color:#539bff;color:var(--background)}}@media(any-hover: none){.table>tbody>tr>td>button:active{border:none;background-color:#539bff;color:var(--background)}}.table>tfoot{color:var(--red);height:35px;vertical-align:bottom}